@import '../core/variables.scss';

.main {
    width: 37.5rem;
    padding-top: 2rem;
    border-left: solid 1px $color-secondary;
    border-right: solid 1px $color-secondary;
}


.mainheader {
    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;
        height: 3.3125rem;
    }

    &__account {
        display: block;
        margin: 0 1rem;
        font-size: 1.25rem;
        font-weight: 700;
        line-height: 0.9;
    }

    &__tweets {
        margin: 0 1rem;
        display: block;
        font-size: 0.8125rem;
        font-weight: 400;
        line-height: 1.5rem;
        color: $color-secondary4;
    }

    &__content {
        margin: 0 1rem;
    }

    &__profile-image {
        position: absolute;
        transform: translate(0, -55%);
        width: 8.75rem;
        height: 8.75rem;
        border-radius: 50%;
        border: 4px solid $color-primary;
        background: $color-primary;

        >* {
            width: 100%;
            border-radius: 50%;
            border: 1px solid $color-secondary;
        }
    }

    &__actions {
        display: flex;
        justify-content: flex-end;
        padding: 0.5rem 0;
        margin-bottom: 2rem;
    }

    &__follow-btn {
        width: 8.75rem;
        height: 2.25rem;
        border-radius: 100px;
        border: 1px solid $color-secondary2;
        background-color: $color-primary;
        font-size: 15px;
        font-weight: 700;
        color: $color-secondary4;
    }

    &__account-info {
        margin: 1rem 0;
    }

    &__acount-title {
        display: block;
        font-size: 1.25rem;
        font-weight: 700;
        line-height: 1.3;
    }

    &__acount-username {
        font-size: 0.9375rem;
        color: $color-secondary4;
    }

    &__acount-follow {
        padding: 1px 4px;
        border-radius: 4px;
        background-color: $color-secondary3;
        font-size: 0.6875rem;
        color: $color-secondary4;
    }

    &__account-description {
        margin-bottom: 1rem;
        font-weight: normal;
        font-size: 0.9375rem;
        line-height: 1.5rem;
    }

    &__account-data {
        margin-bottom: 1rem;
        font-size: 0.9375rem;
        color: $color-secondary4;
    }

    &__account-region,
    &__account-link,
    &__account-date {
        padding: 0 0.5rem 0 1.2rem;
        background-repeat: no-repeat;
        background-position: 0 3px;
        background-size: 14px;
    }

    &__account-region {
        background-image: url('../../images/icon-place.png');
    }

    &__account-link {
        background-image: url('../../images/icon-link.png');
    }

    &__account-date {
        background-image: url('../../images/icon-calendar.png');
    }

    &__followers-info {
        color: $color-secondary4;
        font-size: 0.9375rem;
        padding-bottom: .55rem;

    }

    &__followers {
        padding-left: 2rem;
    }

    &__followers-number {
        font-weight: 700;
        color: $color-primary2;
    }


}